.main_contnet_section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.project_section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.projects_line {
  margin: 130px 0 30px 0;
  border: none;
  background-color: #0E0E0E;
  height: 1px;
  width: 100%;
}

.project_topics {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0 90px;
}

.project_topics_list_item:not(:last-child) {
  margin-right: 25px;
}

.project_topics_list_item {
  color: #0E0E0E;
  text-decoration: none;
  position: relative;
}

.project_topics_list_item::after {
  content: "";
  position: absolute;
  height: 2px;
  background: black;
  width: 0%;
  top: 57%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: width 0.2s linear;
}

.project_topics_list_item.active::after {
  width: 110%;
}

.project_topics_link {
  text-decoration: none;
  color: #0E0E0E;
}

.projects_wrapper {
  column-count: 3;
  column-gap: 30px;
  width: 100%;
}

.projects img {
  min-width: 100%;
}

.projects figure {
  display: grid;
  grid-template-rows: 1fr auto;
  margin-bottom: 30px;
  break-inside: avoid;
}

.projects_button {
  margin: 100px 0 155px;
  text-decoration: none;
  color: #0E0E0E;
  background-color: #f5f5f5;
  padding: 46px 36px;
  border-radius: 100%;
}

.title_offer {
  font-size: 28px;
  font-weight: 500;
  line-height: 44px;
  letter-spacing: 0em;
  max-width: 600px;
  text-align: center;
}

.offer_btn {
  padding: 13px 61px 15px 36px;
  text-decoration: none;
  line-height: 26px;
  position: relative;
  color: #fff;
  background-color: #0e0e0e;
  margin: 50px 0 160px;
}